import React from 'react';

export const netlifyToGatsby = (Template) => {
    return class Transformed extends React.Component {
        transformedProps() {
            const { entry } = this.props;

            const { data } = entry.toJS();

            return {
                data: {
                    markdownRemark: {
                        html: data.body,
                        frontmatter: {
                            ...data
                        }
                    }
                }
            };
        }

        render() {
            return <Template { ...this.transformedProps() } />
        }
    }
};



